@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  
}

header {
  height: 82px;
  background-color: #1f1f1f;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: white;
}
header h1{
  margin-left: 30px;
}

.scoreboard {
  margin-left: 40px;
  background: #1f1f1f;
  color: white;
  display: -webkit-flex;
  display: flex;
  
}
.scoreboard h4 {
  font-size: 20px;
}
.scoreboard span {
  margin-left: 15px;
  margin-right: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-size: 25px;
}


.main-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  
}
.symbol-container {
  margin-top: 50px;
  display: -webkit-flex;
  display: flex;
  max-width: 350px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.symbol-item {
  width: 50px;
  height: 50px;
  border: 1px solid grey;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  cursor: pointer;
  font-size: 30px;
  background: #1f1f1f;
  color: white
}
.symbol-item:hover {
  background: #2c2c2c;
}


.game-over-screen {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
  background: #1f1f1f;
  color: white;
  text-align: center;
}
.game-over-screen h1 {
  font-size: 50px;
}
.game-over-screen h3{
  font-size: 30px;
}
.game-over-screen button {
  width: 100px;
  height: 50px;
  background: rgb(211, 211, 211);
  border: none;
  cursor: pointer;
  font-size: 20px;
  font-weight: bolder;
  border-radius: 5px;
}
.game-over-screen button:hover {
  background: rgb(255, 255, 255);
}
